import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RestApiSQL } from '../service/rest.service';

@Component({
  selector: 'app-patient-crud',
  templateUrl: './patient-crud.component.html',
  styleUrls: ['./patient-crud.component.css']
})

export class PatientCRUDComponent implements OnInit {
  public form!:FormGroup;



  constructor(private RestService: RestApiSQL, private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.form=this.formBuilder.group({
      fullName: [''],
      IdPte: [''],
      phone: [''],
      email: ['']
    })
        
  }


  public actCrearPte(){
    // Cambiar el destino del servidor Backend en poxy.conf.json

    // servidores Backend disponibles:
    // Servidor de prueba en postman https://435daf80-1739-4090-9ef8-d20b100e05af.mock.pstmn.io
    // local http://192.168.0.15:5000/pacientes
    // aws http://18.218.109.116/pacientes

    this.RestService.post('pacientes',  
    {
      fullName:this.form.value.fullName,
      IdPte:this.form.value.IdPte,
      phone:this.form.value.phone,
      email:this.form.value.email
    }
    )
    .subscribe(respuesta => {
      console.log("comentario Enviado");
    })

  }

}
